import { useState, useEffect } from 'react';
import { API_URL } from '@/api/config';

export default function AdminPanel() {
  const [providers, setProviders] = useState([]);

  // Fetch pending providers
  useEffect(() => {
    const fetchProviders = async () => {
      const response = await fetch(`${API_URL}/api/providers/pending`);
      const data = await response.json();
      setProviders(data.providers || []);
    };
    fetchProviders();
  }, []);

  // Approve a provider
  const approveProvider = async (id) => {
    const response = await fetch(`${API_URL}/api/providers/approve?id=${id}`, {
      method: 'PATCH',
    });

    if (response.ok) {
      alert('✅ Provider approved');
      setProviders(providers.filter(p => p.id !== id));
    } else {
      alert('❌ Failed to approve provider');
    }
  };

  return (
    <div style={{ padding: 20 }}>
      <h1>Pending Providers</h1>
      <table border="1" cellPadding="8">
        <thead>
          <tr>
            <th>Name</th>
            <th>Phone</th>
            <th>Branch</th>
            <th>Service</th>
            <th>District</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          {providers.map(provider => (
            <tr key={provider.id}>
              <td>{provider.name}</td>
              <td>{provider.phone}</td>
              <td>{provider.branch || '—'}</td>
              <td>{provider.service || '—'}</td>
              <td>{provider.district || '—'}</td>
              <td>
                <button onClick={() => approveProvider(provider.id)}>
                  Approve
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}
